
import { defineComponent, nextTick, onMounted, ref, watch } from "vue";

import {
  generateGetAlertsTask,
  generateGetSourcesTask,
  generateGetTagsTask,
} from "@/api-helper";
import AlertsComponent from "@/components/alert/Alerts.vue";
import Form from "@/components/alert/Form.vue";
import ErrorMessage from "@/components/ErrorMessage.vue";
import Loading from "@/components/Loading.vue";
import { SearchParams } from "@/types";

export default defineComponent({
  name: "AlertsWrapper",
  components: {
    AlertsComponent,
    Form,
    Loading,
    ErrorMessage,
  },
  setup() {
    const page = ref(1);
    const tag = ref<string | undefined>(undefined);
    const form = ref<InstanceType<typeof Form>>();

    const getAlertsTask = generateGetAlertsTask();
    const getTagsTask = generateGetTagsTask();
    const getSourcesTask = generateGetSourcesTask();

    const getAlerts = async () => {
      const params = form.value?.getSearchParams() as SearchParams;
      return await getAlertsTask.perform(params);
    };

    const updatePage = (newPage: number) => {
      page.value = newPage;
    };

    const resetPage = () => {
      page.value = 1;
    };

    const search = async () => {
      // reset page
      resetPage();

      await getAlerts();
    };

    const updateTag = (newTag: string | undefined) => {
      if (tag.value === newTag) {
        tag.value = undefined;
      } else {
        tag.value = newTag;
      }

      nextTick(async () => await search());
    };

    const refreshPage = async () => {
      // it is just an alias of search
      // this function will be invoked when an alert is deleted
      await search();
    };

    watch(page, async () => {
      nextTick(async () => await getAlerts());
    });

    onMounted(() => {
      getTagsTask.perform();
      getSourcesTask.perform();

      getAlerts();
    });

    return {
      getAlertsTask,
      getSourcesTask,
      getTagsTask,
      refreshPage,
      search,
      tag,
      updatePage,
      updateTag,
      form,
      page,
    };
  },
});
